@import 'sanitize.css/sanitize.css';
@import 'sanitize.css/forms.css';
@import 'sanitize.css/assets.css';
@import 'sanitize.css/typography.css';
@import 'sanitize.css/reduce-motion.css';
:root {
  --color-black: #000;
  --color-white: #fff;
  --body-color: var(--front-primary);
  --body-bg: var(--back-primary);
  --font-base: var(--font-sans);
  --font-sans:
    Inter, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Ubuntu",
    "Droid Sans", "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji",
    "Segoe UI Symbol";
  --font-serif: Georgia, "Times New Roman", Times, serif;
  --font-mono: SFMono-Regular, Menlo, Monaco, Consolas, Liberation Mono, "Courier New", monospace;
  --font-size-root: 100%;
  --font-size-base: 1rem;
  --font-weight-base: 400;
  --line-height-base: 1.25;
}

@media (prefers-color-scheme: light) {
  :root {
    --front-primary: rgb(60 60 67 / 92%);
    --back-primary: #fff;
  }
}
@media (prefers-color-scheme: dark) {
  :root {
    --front-primary: rgb(255 255 255 / 92%);
    --back-primary: #19191e;
  }
}
:root {
  font-size: var(--font-size-root);
}

html,
body {
  height: 100%;
}

html {
  min-width: 320px;
  min-height: 100%;
  font-size: var(--font-size-root);
  font-family: sans-serif;
  line-height: normal;
  -webkit-tap-highlight-color: rgb(var(--color-black) 0);
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

body {
  margin: 0;
  color: var(--body-color);
  font-weight: var(--font-weight-base);
  font-size: var(--font-size-base);
  font-family: var(--font-base);
  line-height: var(--line-height-base);
  background-color: var(--body-bg);
}

svg {
  display: block;
}

[tabindex="-1"]:focus:not(:focus-visible) {
  outline: 0;
}

[hidden] {
  display: none;
}

@font-face {
  font-weight: 400;
  font-family: "Inter";
  font-style: normal;
  font-display: swap;
  src: url("/{{fonts}}/Inter-Regular.woff2") format("woff2");
}
.container {
  max-width: 832px;
  height: 100%;
  margin: 0 auto;
  padding: 0 16px;
}

.footer__inner {
  padding: 8px 0;
}

.header__inner {
  padding: 8px 0;
}

.logo {
  width: 87px;
  height: 24px;
}

.page {
  display: flex;
  flex-direction: column;
  min-height: calc(var(--vh) * 100);
}
.page__header {
  flex: 0 0 auto;
}
.page__main {
  flex: 1 0 auto;
}
.page__footer {
  flex: 0 0 auto;
}

